<template>
    <div class="main-container">
      <div class="box-container">
        <h4 class="text-melon"> </h4>
        <form @submit.prevent="submitInquiry">
          <div>
            <label for="title"></label>
            <input type="text" v-model="inquiry.inquiryTitle" id="title" required>
          </div>
          <div>
            <select v-model="inquiry.inquiryType" id="type" required>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
          </div>
          <div style="margin-top: 5px;">
            <RichTextEditor v-model="inquiry.inquiryContent" @input="validateContent" />
            <div v-if="contentError" style="color: red;"> .</div>
          </div>
          <div class="btn-container">
          <button type="submit" class="btn btn-green"> </button>
          <button @click="goHome" class="btn btn-gray"></button>
          </div>
        </form>
      </div>
    </div>
</template>

<script>
import RichTextEditor from '@/components/RichTextEditor.vue';
import { mapActions, mapGetters, mapState } from 'vuex';
import '../assets/styles.css';

export default {
  components: { RichTextEditor },
  data() {
    return {
      inquiry: {
        inquiryTitle: '',
        inquiryType: '',
        inquiryContent: '',
        inquiryDate: new Date().toISOString(),
        inquiryStatus: ''
      },
      contentError: false
    };
  },
  computed: {
    ...mapState({
      memberUniqueId: state => state.member.memberId
    }),
    ...mapGetters('inquiry', ['allInquiries']),
    inquiryList() {
      console.log('inquiries :', this.allInquiries);
      return this.allInquiries;
    }
  },
  methods: {
    ...mapActions('inquiry', ['createInquiry', 'fetchInquiries']),
    async submitInquiry() {
      //    
      if (!this.validateContent()) {
        this.contentError = true;
        return;
      }
      try {
        this.inquiry.memberUniqueId = this.memberUniqueId;
        console.log(' :', this.inquiry);

        await this.createInquiry(this.inquiry);
        alert(' .');
        this.$router.push('/myinquiries');
      } catch (error) {
        console.error('  :', error);
      }
    },
    validateContent() {
      // HTML    
      const tempDiv = document.createElement('div');
      tempDiv.innerHTML = this.inquiry.inquiryContent;
      const textContent = tempDiv.textContent || tempDiv.innerText || "";

      //         
      if (!textContent.trim().length) {
        this.contentError = true;
        return false;
      }

      //   
      this.contentError = false;
      return true;
    },
    goHome() {
      this.$router.push('/');
    },
  }
};
</script>

<style src="../assets/styles.css"></style>
