<template>
  <div class="overlay" @click="closeMenu">
    <div class="menu" @click.stop>
      <div class="logo-wrapper">
        <router-link to="/">
          <img src="../assets/pillgood_logo.png" class="logo-image">&nbsp;
        </router-link>
      </div>

      <ul>
        <li><a href="/mypage"></a></li>
        <li><a href="/productlist?sort=latest" @click="navigateTo($event, { name: 'ProductList', query: { sort: 'latest' } })"></a></li>
        <li><a href="/productlist?sort=topSelling" @click="navigateTo($event, { name: 'ProductList', query: { sort: 'topSelling' } })"></a></li>
        <li><a href="/productlist"></a></li>
        <li><a href="/productlist?tab=nutrients" @click="navigateTo($event, { name: 'ProductList', query: { tab: 'nutrients' } })"></a></li>
        <li><a href="/productlist?tab=deficiencies" @click="navigateTo($event, { name: 'ProductList', query: { tab: 'deficiencies' } })"></a></li>
        <li><a href="/productlist?tab=targets" @click="navigateTo($event, { name: 'ProductList', query: { tab: 'targets' } })"></a></li>
        <li><a href="/noticelist"></a></li>
        <li><a href="/inquiries"></a></li>
        <li><a href="#contact">Contact</a></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HamburgerMenu',
  methods: {
    closeMenu() {
      this.$emit('close');
    },
    navigateTo(event, location) {
      event.preventDefault();  //   
      this.$router.push(location);
      this.closeMenu();
    }
  }
};
</script>

<style scoped>
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: flex-start; /*   */
}
.menu {
  background-color: white;
  width: 250px;
  height: 100%;
  box-shadow: 2px 0 5px rgba(0,0,0,0.5);
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.logo-wrapper {
  width: 80%;
  margin: 0 auto; /*   */
  margin-bottom: 20px; /*       */
}

.logo-image {
  width: 100%;
  height: auto; /*    */
}

.menu ul {
  padding: 0;
  margin: 0;
  list-style-type: none; /*   */
}
.menu li {
  padding: 10px 0;
}
</style>