<template>
  <div class="main-container">
    <h2>/  </h2>
    <div v-if="refunds.length === 0">   .</div>
    <ul v-else>
      <li v-for="refund in refunds" :key="refund.refundId">
         : {{ refund.orderNo }} /  : {{ refund.orderTotalAmount }} /  : {{ refund.totalRefundAmount }} /  : {{ refund.refundStatus }} 
      </li>
    </ul>
  </div>
</template>

<script>
import axios from '../axios';

export default {
  props: {
    orderNo: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      refunds: []
    };
  },
  async created() {
    try {
      const response = await axios.get(`/api/refunds/order/${this.orderNo}`);
      console.log(' :', response.data);
      this.refunds = response.data;
    } catch (error) {
      console.error('     :', error);
    }
  }
};
</script>

<style scoped>
.main-container {
  padding: 20px;
}
</style>
