<template>
  <div class="box-container box-shadow">
    <h4 class="text-melon"> </h4>
    <form @submit.prevent="handleForgotPassword">
      <table>
        <tr>
          <td><label for="email" class="text-melon"></label></td>
          <td><input type="email" id="email" v-model="email" required /></td>
        </tr>
      </table>
      <div class="btn-container">
        <button type="submit" class="btn btn-green"> </button>
        <router-link to="/login" class="btn btn-gray"> </router-link>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ForgotPasswordView',
  data() {
    return {
      email: ''
    }
  },
  methods: {
    async handleForgotPassword() {
      try {
        await axios.post('/api/members/forgotpassword', { email: this.email });
        alert('  .');
        this.$router.push('/resetpassword');
      } catch (error) {
        console.error('  : ', error);
        alert('   .');
      }
    }
  }
}
</script>

