// src/main.js ( )
import { createApp } from 'vue';
import App from './App.vue';
import router from './router/index';
import store from './store'; // Vuex  
import { QuillEditor } from '@vueup/vue-quill';
import '@vueup/vue-quill/dist/vue-quill.snow.css'; // CSS 
import { setupCalendar, Calendar, DatePicker } from 'v-calendar';
import 'v-calendar/dist/style.css'; // CSS
import BackToTop from './components/BackToTop.vue';
import axios from 'axios';
axios.defaults.baseURL = process.env.VUE_APP_API_URL || 'http://localhost:9095'; //  URL 

// Kakao SDK 
const kakaoScript = document.createElement('script');
kakaoScript.src = 'https://developers.kakao.com/sdk/js/kakao.js';
kakaoScript.onload = () => {
  /* global Kakao */ // Kakao   
  Kakao.init('03f074279f45f35b6bed2cfbcc42ec4d'); //     JavaScript 
  console.log('Kakao SDK initialized');
};
document.head.appendChild(kakaoScript);

console.log(QuillEditor); // QuillEditor   

const app = createApp(App);

setupCalendar(app, {
  componentPrefix: 'vc' // v-calendar  vc-calendar 
})

// QuillEditor   
app.component('QuillEditor', QuillEditor);

// V-calendar  
app.component('VCalendar', Calendar);
app.component('VDatePicker', DatePicker);

// BackToTop   
app.component('BackToTop', BackToTop);

// Vuex Router    
app.use(store);
app.use(router);
app.mount('#app');
