<template>
  <div class="main-container">
    <div class="login-box">
      <!--  -->
      <div class="form-container">
        <!--    -->
        <form @submit.prevent="handleLogin" class="login-form">
          <h4 class="text-melon"></h4>
          <div class="input-container">
            <label for="email" class="text-melon"></label>
            <input type="email" id="email" v-model="email" required />
          </div>
          <div class="input-container">
            <label for="password" class="text-melon"></label>
            <input type="password" id="password" v-model="password" required />
          </div>
          <div class="btn-container">
            <button type="submit" class="btn btn-green"></button>
          </div>
        </form>
        <!--    -->
        <div class="vertical-line"></div>
        <!--     -->
        <div class="social-container">
          <h4 class="text-melon"> </h4>
          <KakaoLogin @loginSuccess="handleKakaoLoginSuccess" />
        </div>
      </div>
      <!--  /  -->
      <div class="btn-container2">
        <button type="button" class="btn btn-gray" @click="navigateToRegister"></button>
      </div>
      <div class="check-container">
        <input id="chk_all" type="checkbox" v-model="rememberEmail">
        <label for="chk_all" class="text-gray"> </label>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <router-link to="/forgotpassword"> </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import axios from '../axios'
import KakaoLogin from '../components/KakaoLogin.vue'; //    
import '../assets/styles.css';

function saveEmailToLocalStorage(email) {
  localStorage.setItem('rememberedEmail', email);
}

function getEmailFromLocalStorage() {
  return localStorage.getItem('rememberedEmail') || '';
}

function removeEmailFromLocalStorage() {
  localStorage.removeItem('rememberedEmail');
}

export default {
  name: 'LoginView',
  components: {
    KakaoLogin //    
  },
  mounted() {
    this.checkSessionStatus();
    this.email = getEmailFromLocalStorage(); //      
    this.handleKakaoCallback(); //     
  },
  data() {
    return {
      password: '',
      loginError: '', //     
      rememberEmail: !!localStorage.getItem('rememberedEmail'),
      email: localStorage.getItem('rememberedEmail') || ''
    }
  },
  methods: {
    ...mapActions('member', ['login', 'clearUserState']), // 'member'   
    async handleLogin() {
      console.log(' : ', this.email, this.password);
      try {
        await this.login({ email: this.email, password: this.password });
        if (this.$store.state.member.isLoggedIn) {
          console.log(' ');
          alert(' ');
          if (this.rememberEmail) {
            saveEmailToLocalStorage(this.email); //     
          } else {
            removeEmailFromLocalStorage(); //      
          }
          this.$router.push('/'); //     
        } else {
          this.loginError = ' .  .';
          alert(' .  .');
        }
      } catch (error) {
        this.loginError = '   .';
      }
    },
    navigateToRegister() {
      this.$router.push('/register');
    },
    async checkSessionStatus() {
      try {
        const response = await axios.get('/api/members/status', { withCredentials: true });
        console.log("  : ", response.data);
        if (!response.data.isLoggedIn) {
          this.$store.dispatch('clearUserState');
        } else {
          this.$store.commit('setLoginState', response.data);
        }
      } catch (error) {
        console.error("   : ", error);
        this.$store.dispatch('clearUserState');
      }
    },
    async handleKakaoCallback() { //      
      const code = this.$route.query.code;
      if (code) {
        console.log(' :', code);
        try {
          const response = await fetch('https://kauth.kakao.com/oauth/token', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
            },
            body: new URLSearchParams({
              grant_type: 'authorization_code',
              client_id: '03f074279f45f35b6bed2cfbcc42ec4d', // REST API 
              redirect_uri: 'http://localhost:8080/login', //  URI
              code: code,
            }).toString(),
          });

          const data = await response.json();
          if (data.access_token) {
            //       .
            const result = await axios.post('/api/social/kakao/callback', { accessToken: data.access_token });
            console.log(result.data);
            //    
            if (result.data.success) {
              this.$store.commit('member/setLoginState', {
                isLoggedIn: true,
                memberId: result.data.memberId,
                member: result.data.member,
                isAdmin: result.data.isAdmin
              });
              this.$router.push('/');
            } else {
              alert('  ');
            }
          } else {
            console.error('Failed to fetch access token:', data);
          }
        } catch (error) {
          console.error('Error fetching token:', error);
        }
      }
    },
    handleKakaoLoginSuccess() {
      //    
      console.log('  ');
    }
  },
  watch: {
    'member.isLoggedIn'(newVal) { // 'member'  isLoggedIn   
      console.log('  :', newVal);
    }
  }
};
</script>

<style scoped>
.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
}

.login-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.form-container,
.social-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-form {
  width: 100%;
}

.input-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.input-container label {
  margin-bottom: 5px;
}

.input-container input {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.btn-container {
  display: flex;
  justify-content: center;
}

.btn-container2 {
  text-align: center;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.check-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.vertical-line {
  width: 80%;
  height: 1px;
  background-color: #B4D9A9;
  margin: 20px 0;
}

@media (min-width: 1200px) {
  .login-box {
    flex-direction: column; /*     */
    justify-content: space-between;
    max-width: 1200px;
    height: auto;
  }

  .form-container {
    display: flex;
    flex-direction: row; /*   */
    justify-content: space-between;
    width: 100%;
  }

 .vertical-line {
  width: 20%;
  height: 1px;
  background-color: #B4D9A9;
  rotate: 90deg;
}

  .form-container .login-form,
  .form-container .social-container {
    width: 45%;
  }

  .btn-container2,
  .check-container {
    width: 100%;
    text-align: center;
  }

  .check-container {
    justify-content: center;
  }
}
</style>
