<template>
  <div class="main-container">
    <h2 class="text-melon"> </h2>
    <!-- mypage nav bar -->
    <div class="btn-container">
      <button @click="checkSurveyResult" class="btn-link">  </button>
      <router-link to="/order-history" class="btn-link"> | </router-link>
      <router-link to="/#" class="btn-link"> </router-link>
      <router-link to="/mycoupon" class="btn-link"> </router-link>
      <router-link to="/reviewlist" class="btn-link"></router-link>
      <router-link to="/myinquiries" class="btn-link">1:1 </router-link>
    </div>
    <div class="box-container-no-shade">
      <div>
        <div v-if="user">
          <table class="mypage">
            <tr>
              <td><label for="email"> </label></td>
              <td><input type="email" v-model="user.email" :readonly="!isEditing"/></td>
            </tr>
            <tr>
              <td><label for="name"> </label></td>
              <td><input type="text" v-model="user.name" :readonly="!isEditing"/></td>
            </tr>
            <tr>
              <td><label for="age"> </label></td>
              <td><input type="text" :value="user.age" readonly/></td>
            </tr>
            <tr>
              <td><label for="gender"> </label></td>
              <td>
                <select v-model="user.gender" v-if="isEditing" required>
                  <option value="N"> </option>
                  <option value="M"></option>
                  <option value="F"></option>
                </select>
                <input type="text" :value="user.gender" v-else readonly/>
              </td>
            </tr>
            <tr>
              <td><label for="phoneNumber"> </label></td>
              <td><input type="text" v-model="user.phoneNumber" :readonly="!isEditing"/></td>
            </tr>
            <tr>
              <td><label for="subscriptionStatus">  </label></td>
              <td><input type="text" :value="subscriptionStatusText" readonly/></td>
            </tr>
            <tr>
              <td><label for="registrationDate">  </label></td>
              <td><input type="text" :value="formatDate(user.registrationDate)" readonly/></td>
            </tr>
          </table>
          <div class="btn-container">
            <button v-if="!isEditing" class="btn btn-green" @click="showPasswordPrompt"></button>
            <button v-else class="btn btn-green" @click="updateMember"></button>
            <button v-if="isEditing" class="btn btn-gray" @click="cancelEdit"></button>
          </div>
        </div>
        <div v-else>
          <p>Loading...</p>
        </div>
      </div>
    </div>

    <!--    -->
    <div v-if="showPasswordModal" class="modal">
      <div class="box-container box-shadow">
        <h4 class="text-melon"> </h4>
        <input type="password" v-model="password" placeholder=" " />
        <div class="btn-container">
          <button class="btn btn-green" @click="verifyPassword"> </button>
          <button class="btn btn-gray" @click="closePasswordModal"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '../axios';
import { useRouter } from 'vue-router';
import { mapState, mapActions, useStore } from 'vuex';
import '../assets/styles.css';

export default {
  name: 'MyPage',
  setup() {
    const store = useStore();
    const router = useRouter();

    const checkSurveyResult = async () => {
      try {
        const memberId = store.state.member.memberId;
        const response = await axios.get(`/api/surveys/member/${memberId}`);
        
        if (response.data && response.data.length > 0) {
          router.push({ name: 'SurveyResult', params: { memberId } });
        } else {
          alert('     ');
          router.push({ name: 'Survey' });
        }
      } catch (error) {
        console.error('Failed to fetch survey result:', error);
        alert('     .');
        router.push({ name: 'Survey' });
      }
    };

    return {
      store,
      checkSurveyResult
    };
  },
  data() {
    return {
      isEditing: false,
      password: '',
      showPasswordModal: false,
      errors: {}
    };
  },
  computed: {
    ...mapState('member', ['member']),
    user() {
      return this.member;
    },
    subscriptionStatusText() {
      return this.user.subscriptionStatus ? ' ' : '  ';
    }
  },
  created() {
    this.fetchUserProfile();
  },
  methods: {
    ...mapActions('member', ['fetchUserProfile']),
    toggleEdit() {
      this.isEditing = !this.isEditing;
    },
    showPasswordPrompt() {
      this.password='';
      this.showPasswordModal = true;
    },
    closePasswordModal() {
      this.showPasswordModal = false;
    },
    async verifyPassword() {
      try {
        const verifyResponse = await axios.post('/api/members/verifyPassword', {
          memberId: this.user.memberUniqueId,
          password: this.password,
        });
        if (verifyResponse.status === 200) {
          this.isEditing = true;
          this.showPasswordModal = false;
        } else {
          alert('  .');
        }
      } catch (error) {
        console.error('Error verifying password:', error);
        alert('  .');
      }
    },
    async updateMember() {
      try {
        const response = await axios.put(`/api/members/update/${this.user.memberUniqueId}`, this.user);
        if (response.status === 200) {
          this.user = response.data;
          this.isEditing = false;
          alert('   .');
        } else {
          console.error('Failed to update member information');
        }
      } catch (error) {
        console.error('Error updating member information:', error);
        alert('   .');
      }
    },
    cancelEdit() {
      this.isEditing = false;
      this.fetchUserProfile();
    },
    formatDate(date) {
      if (!date) return '';
      return new Date(date).toISOString().split('T')[0];
    }
  }
};
</script>

