<template>
  <div class="main-container">
    <h1 class="text-melon"> </h1>
    <form @submit.prevent="submitNotice">
      <table>
        <tr>
          <td>:</td>
          <td><input v-model="noticeTitle" type="text" required /></td>
        </tr>
        <tr>
          <td> (Quill ):</td>
          <td>
            <RichTextEditor v-model="noticeContent" />
          </td>
        </tr>
      </table>
      <div class="btn-container">
        <button type="submit" class="btn btn-green">Create Notice</button>
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import RichTextEditor from '@/components/RichTextEditor.vue'; // RichTextEditor  

export default {
  name: 'NoticeCreate',
  components: {
    RichTextEditor // RichTextEditor  
  },
  data() {
    return {
      noticeTitle: '',
      noticeContent: ''
    };
  },
  methods: {
    ...mapActions('notice', ['createNotice']),
    async submitNotice() {
      try {
        console.log(' :', { noticeTitle: this.noticeTitle, noticeContent: this.noticeContent });
        await this.createNotice({ noticeTitle: this.noticeTitle, noticeContent: this.noticeContent });
        this.$router.push('/noticelist');
      } catch (error) {
        console.error('  :', error);
      }
    }
  }
};
</script>

<style src="../assets/styles.css"></style>