<template>
  <div class="main-container box-shadow">
    <h1 class="text-melon">  </h1>
    <form @submit.prevent="saveMember">
      <table class="line-table">
        <tr>
          <td></td>
          <td><input v-model="member.name" type="text" required /></td>
          <td></td>
          <td><input v-model="member.age" type="number" required /></td>
          <td></td>
          <td>
            <select v-model="member.gender" required>
              <option value="F"></option>
              <option value="M"></option>
            </select>
          </td>
          <td></td>
          <td>
            <select v-model="member.subscriptionStatus">
              <option :value="true"></option>
              <option :value="false">-</option>
            </select>
          </td>
        </tr>
        <tr>
          <td></td>
          <td><input v-model="member.email" type="email" required /></td>
          <td></td>
          <td><input v-model="member.phoneNumber" type="text" required /></td>
          <td></td>
          <td>
            <select v-model="member.memberLevel" required>
              <option value="ADMIN"></option>
              <option value="USER"></option>
            </select>
          </td>
          <td></td>
          <td></td>
        </tr>
      </table>
      <div class="btn-container">
        <button type="submit" class="btn btn-green"></button>
        <button type="button" class="btn btn-gray" @click="cancelEdit"></button>
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
  name: 'MemberEdit',
  data() {
    return {
      member: {
        name: '',
        age: '',
        email: '',
        phone: '',
        gender: '',
        subscriptionStatus: false,
        memberLevel: ''
      }
    };
  },
  computed: {
    ...mapState('member', ['editingMember'])
  },
  created() {
    if (this.editingMember) {
      console.log('MemberEdit  memberId:', this.editingMember.memberId);  //   
      this.member = { ...this.editingMember };
    } else {
      this.$router.push('/memberlist');
    }
  },
  methods: {
    ...mapActions('member', ['updateMember']),
    async saveMember() {
      try {
        await this.updateMember(this.member);
        this.$router.push('/memberlist');
      } catch (error) {
        console.error('     :', error);
      }
    },
    cancelEdit() {
      this.$router.push('/memberlist');
    }
  }
};
</script>

<style>
.line-table td input {
  width: 100%;
  margin: 5px auto;
  padding: 3px;
}
</style>
