<template>
  <div class="main-container">
    <h2>  </h2>
    <div v-if="refunds.length === 0">   .</div>
    <ul v-else>
      <li v-for="refund in refunds" :key="refund.refundId">
         : {{ refund.orderNo }} /  : {{ refund.totalRefundAmount }} /  : {{ refund.refundStatus }}
        <select v-model="refund.refundStatus" @change="updateRefundStatus(refund)">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from '../axios';

export default {
  data() {
    return {
      refunds: []
    };
  },
  async created() {
    try {
      const response = await axios.get('/api/refunds/list');
      this.refunds = response.data;
    } catch (error) {
      console.error('     :', error);
    }
  },
  methods: {
    async updateRefundStatus(refund) {
      try {
        await axios.put(`/api/refunds/update-status/${refund.refundId}`, refund.refundStatus);
        console.log('   .');
      } catch (error) {
        console.error('     :', error);
      }
    }
  }
};
</script>

<style scoped>
.main-container {
  padding: 20px;
}
</style>
