<template>
  <div class="main-container">
    <h2 class="text-melon"> </h2>
    <table class="line-table">
      <thead>
        <tr>
          <th>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</th>
          <th colspan="2"> </th>
          <th> </th>
          <th></th>
          <th> </th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="review in filteredReviews" :key="review.reviewId">
          <td>{{ review.reviewId }}</td>
          <td>
            <img style="height: 100px; width: auto;" :src="review.product.productImage" alt="Prod uct Image" />
          </td>
          <td>{{ truncateText(review.product.productName, 15) }}</td>
          <td @click="goToReviewDetail(review)" style="cursor: pointer; white-space: nowrap; display: inline-flex;">
            <span v-html="truncateText(extractText(review.reviewContent), 20)"></span>
            <i v-if="containsImageTag(review.reviewContent)" class="fa-solid fa-paperclip"></i>
          </td>
          <td>
            <star-rating v-model="review.rating" :star-size="30" :show-rating="false" :disable-click="true"></star-rating>
          </td>
          <td>{{ review.memberName }}</td>
          <td>{{ formatDate(review.reviewDate) }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from '../axios'; //  axios  import
import '../assets/styles.css';
import { mapState, mapActions } from 'vuex'; // Vuex   import
import StarRating from 'vue3-star-ratings'; // StarRating  import

export default {
  name: 'ReviewList',
  components: {
    StarRating, // StarRating  
  },
  data() {
    return {
      selectedCoupons: {}, //   ID     
    };
  },
  computed: {
    ...mapState('member', ['members', 'isAdmin', 'memberId']), // Vuex  members isAdmin, memberId 
    ...mapState('coupon', ['activeCoupons']), // Vuex  coupons 
    ...mapState('review', ['reviews']), // Vuex  reviews 
    filteredReviews() {
      let reviews;
      // ADMIN     ,      
      if (this.isAdmin) {
        reviews = this.reviews;
      } else {
        reviews = this.reviews.filter(review => review.memberUniqueId === this.memberId);
      }
      //     
      return reviews.sort((a, b) => new Date(b.reviewDate) - new Date(a.reviewDate));
    }
  },
  created() {
    console.log(' '); //   
    this.fetchReviews(); //      
    this.fetchActiveCoupons(); // couponStore coupons 
  },
  methods: {
    ...mapActions('coupon', ['fetchActiveCoupons', 'createOwnedCoupon']), // coupon   
    ...mapActions('review', ['fetchReviews']), // review   
    async issueCoupon(reviewId, memberUniqueId, couponId) {
      if (!couponId) {
        alert(' .');
        return;
      }

      const ownedCoupon = {
        memberUniqueId,
        couponId,
      };

      try {
        await this.createOwnedCoupon(ownedCoupon);
        alert(' .');
        this.updateReviewCouponIssued(reviewId); //     
      } catch (error) {
        console.error('  :', error);
        alert('  .');
      }
    },
    async updateReviewCouponIssued(reviewId) {
      try {
        const index = this.reviews.findIndex(review => review.reviewId === reviewId);
        console.log('Updating review at index:', index); //  

        if (index !== -1) {
          const updatedReview = {
            ...this.reviews[index],
            couponIssued: true
          };
          this.reviews.splice(index, 1, updatedReview); //  
          console.log('Updated review:', this.reviews[index]); //   
          
          //   
          await axios.put('/admin/reviews/update-coupon-issued', null, {
            params: {
              reviewId,
              couponIssued: true
            }
          });
          console.log('Coupon issued status updated on server');
        } else {
          console.error('Review not found for ID:', reviewId); //       
        }
      } catch (error) {
        console.error('Error updating review couponIssued status:', error); //     
        throw error; //   
      }
    },
    async fetchOrderDetailById(orderDetailNo) {
      try {
        const response = await axios.get(`/api/order-details/${orderDetailNo}`); // orderDetailNo orderDetail  
        return response.data; // orderDetail  
      } catch (error) {
        console.error(`Failed to fetch order detail for order detail ${orderDetailNo}:`, error);
        return null;
      }
    },
    async fetchOrderByOrderNo(orderNo) {
      try {
        const response = await axios.get(`/api/orders/${orderNo}`); // orderNo order  
        return response.data; // order  
      } catch (error) {
        console.error(`Failed to fetch order for order number ${orderNo}:`, error);
        return null;
      }
    },
    getMemberName(memberId) {
      const member = this.members.find(member => member.memberUniqueId === memberId); // memberId   
      return member ? member.name : '  ';
    },
    formatDate(date) {
      if (!date) return '';
      return new Date(date).toLocaleDateString(); //     
    },
    extractText(content) {
      // <p>   HTML    
      return content.replace(/<\/?p>/g, '').replace(/<\/?[^>]+(>|$)/g, "").trim();
    },
    containsImageTag(content) { //     
      return /<img[^>]*src="[^"]*"[^>]*>/g.test(content);
    },
    truncateText(text, maxLength) {
      if (text.length <= maxLength) {
        return text;
      }
      return text.substring(0, maxLength) + '...';
    },
    async goToReviewDetail(review) {
      const orderDetail = await this.fetchOrderDetailById(review.orderDetailNo);
      const order = await this.fetchOrderByOrderNo(orderDetail.orderNo);
      const queryParams = {
        reviewId: review.reviewId,
        orderNo: orderDetail.orderNo,
        orderDate: order.orderDate,
        productId: review.productId,
        productName: review.product.productName,
        productImage: review.product.productImage,
        orderDetailNo: review.orderDetailNo,
        reviewContent: review.reviewContent,
        rating: review.rating
      };
      console.log("  :", queryParams);

      this.$router.push({
        name: 'ReviewDetail',
        query: queryParams
      });
    }
  }
};
</script>

<style scoped>
 .line-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 30px;
  }

  .line-table td, tr, th {
    border: none;
  }
</style>
