<template>
    <div class="main-container box-shadow">
      <h4 class="payment-title text-melon"> </h4>
      <div v-if="currentOrder">
        <div class="order-summary">
          <p>: {{ currentOrder.orderNo }}</p>
          <p>: {{ currentOrder.recipient }}</p>
          <p> : {{ totalAmount }} </p>
          <!--      -->
        </div>
        <button @click="approvePayment" class="btn btn-green"> </button>
      </div>
      <div v-else>
        <p> ...</p>
      </div>
    </div>
  </template>
  
  <script>
  import { mapState, mapGetters, mapActions } from 'vuex';
  import axios from 'axios';
  
  export default {
    name: 'ApprovePayment',
    data() {
      return {
        currentOrder: JSON.parse(localStorage.getItem('currentOrder')) || null
      };
    },
    computed: {
      ...mapGetters('member', ['memberId']),
      ...mapState('order', {
        user: state => state.userProfile,
      }),
      totalAmount() {
        return this.currentOrder ? this.currentOrder.totalAmount : 0; //      .
      }
    },
    async created() {
      if (!this.currentOrder) {
        await this.fetchUserProfile(); //   .
        await this.fetchOrderDetails(); //   .
      }
      console.log('Current Order in ApprovePayment (created):', this.currentOrder); //  
    },
    methods: {
      ...mapActions('order', ['fetchUserProfile', 'fetchOrderDetails']),
      async approvePayment() {
        try {
          const response = await axios.post('/api/payment/confirm-billing', {
            amount: this.totalAmount,
            orderId: this.currentOrder.orderNo, //    .
            orderName: '  ',
            customerKey: this.memberId,
            customerEmail: this.user.email,
            customerName: this.user.name
          });
          if (response.status === 200) {
            localStorage.setItem('paymentResponse', JSON.stringify(response.data)); // response  localStorage 
            this.$router.push({ name: 'BillingSuccess' }); //    
          } else {
            console.error('   :', response);
          }
        } catch (error) {
          console.error('     :', error);
        }
      }
    }
  };
  </script>
  
