<template>
  <div class="main-container">
    <h2 class="text-melon">  </h2>
    <div class="box-container-no-shade">
      <div class="input-group">
        <label for="paymentKey"> </label>
        <input type="text" id="paymentKey" v-model="paymentKey" readonly />
      </div>
      <div class="input-group">
        <label for="cancelAmount"> </label>
        <input type="number" id="cancelAmount" v-model="cancelAmount" readonly/>
      </div>
      <div class="input-group">
        <label for="cancelReason"> </label>
        <input type="text" id="cancelReason" v-model="cancelReason" />
      </div>
      <button class="btn btn-green" @click="cancelPayment"> </button>
    </div>
  </div>
</template>

<script>
import axios from '../axios'; // 'axios.js'   import

export default {
  data() {
    return {
      cancelReason: '',
      orderNo: this.$route.params.orderNo,
      paymentKey: null,
      cancelAmount: null,
    };
  },
  async created() {
    await this.fetchPaymentKey();
  },
  methods: {
    async fetchPaymentKey() {
      try {
        const response = await axios.get(`/api/payment/payment-info/${this.orderNo}`);
        if (response.status === 200 && response.data) {
          this.paymentKey = response.data.paymentNo; //     .
          this.cancelAmount = response.data.amount
        } else {
          console.error('     :', response);
        }
      } catch (error) {
        console.error('     :', error);
      }
    },
    async cancelPayment() {
      try {
        const response = await axios.post('/api/payment/cancel', {
          paymentKey: this.paymentKey,
          cancelReason: this.cancelReason
        });
        if (response.status === 200) {
          this.$router.push({ name: 'CancelSuccess' }); //     
        } else {
          console.error('   :', response);
        }
      } catch (error) {
        console.error('     :', error);
      }
    }
  }
};
</script>

