<template>
  <div class="box-container box-shadow">
    <h4 class="text-melon"> </h4>
    <form @submit.prevent="handleResetPassword">
      <table>
        <tr>
          <td><label for="newPassword" class="text-melon"> </label></td>
          <td><input type="password" id="newPassword" v-model="newPassword" required /></td>
        </tr>
        <tr>
          <td><label for="confirmPassword" class="text-melon">  </label></td>
          <td><input type="password" id="confirmPassword" v-model="confirmPassword" required /></td>
        </tr>
      </table>
      <div class="btn-container">
        <button type="submit" class="btn btn-green"> </button>
        <router-link to="/login" class="btn btn-gray"> </router-link>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ChangePasswordView',
  data() {
    return {
      newPassword: '',
      confirmPassword: '',
      token: this.$route.query.token // URL   
    }
  },
  methods: {
    async handleResetPassword() {
      if (this.newPassword !== this.confirmPassword) {
        alert('  .');
        return;
      }
      try {
        await axios.post('/api/members/resetpassword', { token: this.token, newPassword: this.newPassword });
        alert(' .');
        this.$router.push('/login');
      } catch (error) {
        console.error('  : ', error);
        if (error.response && error.response.data) {
          if (error.response.data === '  .') {
            alert('   .  .');
          } else {
            alert(error.response.data);
          }
        } else {
          alert('  .');
        }
      }
    }
  }
}
</script>